<template>
  <div id="root">
    <PublicHeader/>
    <transition name="route" mode="out-in">
      <router-view/>
    </transition>
  </div>
</template>

<script>
import PublicHeader from '@/components/public/PublicHeader'
export default {
  name: 'app',
  components: {
    PublicHeader
  },
  mounted(){
    this.change()
  },
  updated(){
    this.change()
  },
  methods:{
    change(){
      if(this.$route.path != '/' || window.matchMedia('(max-width: 512px)'))
      document.querySelector('.header-info').style.background = '#111'
      else
      document.querySelector('.header-info')
      .style.background = 'rgba(0,0,0,.5)'
    }
  }
}
</script>

<style>

</style>



