<template>
  <div id="home">
      <!-- <PublicHeader/> -->
      <PublicBanner/>
      <div class="introduction">
          <h1>Kronos是什么？</h1>
          <div class="featrues">
              <div v-for="(item, index) in items" class="wow fadeIn" :data-wow-delay="index/2+'s'">
                  <h1>{{item.title}}</h1>
                  <p>{{item.summary}}</p>
              </div>
          </div>
          <div class="work">
            <div>
              <h1>让工作变得简单</h1>
              <p>在工作中各种零碎的琐事会让你不免有些焦头烂额，随身携带记事本不仅不方便还没有快速查阅功能</p>
              <router-link to="#">发现更多</router-link>
            </div>
          </div>
      </div>
      <PublicFooter/>
  </div>
</template>

<script>
    import PublicBanner from '@/components/public/PublicBanner'
    import PublicFooter from '@/components/public/PublicFooter'
    
    export default {
        name: 'home',
        components: {
            PublicBanner,
            PublicFooter
        },
        data() {
            return {
                items: [
                    {
                        title: "开源的",
                        summary: "Kronos时间管理器,遵循于GPL V2协定为互联网上的所有用户开放其源代码，Kronos希望有更多的开发者加入其中，使其变为最好用的时间管理器"
                    },
                    {
                        title: "强大的",
                        summary: "Kronos时间管理器,在依据简约设计理念的同时做到满足日常生活中的多种应用场景，做到简约但不简单"
                    },
                    {
                        title: "轻量的",
                        summary: "Kronos时间管理器，基于新一代WEB应用技术能够在任何支持Browser技术的设备上流畅运行"
                    },
                ]
            }
        }
    }
</script>

<style scoped>
</style>





