<template>
    <div id="public-form">
        <h1>登录</h1>
        <form>
            <input v-model="userName" type="text" placeholder="用户名">
            <input v-model="passWord" type="password" placeholder="密码">
            <input type="button" @click="GetNewsData()" value="登陆">
        </form>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                userName: "",
                passWord: "",
                message: "",
            }
        },
        methods: {
            GetNewsData(){
          this.$http.get('//localhost/method/signin.php',{
            params: {
              userName: this.userName,
              passWord: this.passWord
            }
          })
          .then(success => {
            this.message = success.body
            console.log(success)
            console.log(success.data)
            //console.log(this.message)
          })
          .catch(error => {
            alert(JSON.stringify(error))
          })
        }
        },
    }
</script>

<style scoped>
    .form-enter-active{
        opacity: 1;
        transition: all .5s ease;
    }
    .form-leave-active{
        opacity: 0;
        transition: all  .5s ease;
    }
    .form-enter{
        opacity: 0;
    }
    .form-leave{
        opacity: 1;
    }
</style>





