<template>
    <div id="blog">
        <div class="main">
            <div class="index">
                <h1>{{text.title}}<span>{{text.date}}</span></h1>
                <div class="tag">
                    <span v-for="tag in text.tag">{{tag}}</span>
                </div>
                <div class="content" v-html="text.content"></div>
            </div>
            <div class="archive">
                <div>
                    <h1>归档</h1>
                    <ul>
                        <li
                            v-for="(item, index) in blogs"
                        >
                            <span>{{item.date}}</span>
                            <router-link :to="{name: 'Details', params:{id: item.ID}}">
                                {{item.title}}
                            </router-link>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <PublicFooter/>
    </div>
</template>

<script>
    import PublicFooter from '@/components/public/PublicFooter'
    export default {
        components: {
            PublicFooter
        },
        beforeUpdate(){
            for(let i = 0; i < this.blogs.length; i++){
                if(this.$route.params.id == this.blogs[i].ID){
                    this.text = this.blogs[i]
                    console.log(this.text)
                }
            }
        },
        mounted(){
            this.$http.get('./static/data/blogs.json')
            .then(response => {
            console.log(response)
            for(let i = 0; i < response.data.length; i++){
                if(this.$route.params.id == response.data[i].ID){
                    this.text = response.data[i]
                    this.blogs = response.data
                    console.log(response.data[i])
                }
            }
            console.log(this.text.title)
            })
            .catch(function (error) {
            console.log(error);
            });
        },
        data() {
            return {
                text: {},
                blogs: []
            }
        }
    }
</script>

<style>
    .content > p{
        text-indent: 2rem; 
        margin-top: 1rem; 
        line-height: 1.5rem;
    }
</style>

<style scoped>
    .index > h1{
        padding: 1rem 0rem;
    }
    .content{
        padding: 1rem 0rem;
    }
</style>



