<template>
    <div id="public-banner">
        <transition-group tag="ul" class="slide-ul" name="list" mode="out-in">
            <li 
                v-for="(item, index) in items"
                v-show="index===currentIndex"
                :key="index"
                >
                <transition name="infor">
                    <div class="banner-info">
                    <h1>{{item.title}}</h1>
                    <p>{{item.summary}}</p>
                    <div><a :href="item.href">{{item.buttom}}</a></div>
                    
                    </div>
                </transition>
                <img :src="item.imgsrc">
            </li>
        </transition-group>
        <div class="banner-bar">
            <li
            class="banner-bar-li"
            v-for="(item, index) in items"
            :class="{'active':index===currentIndex}"
            ></li>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                items: [
                    {
                        title: 'Kronos时间管理器',
                        summary: '从现在开始获取Kronos时间管理器开始记录任何你想记录的事情',
                        buttom: '立即下载',
                        href: "static/Kronos-v0.0.1-installer.exe",
                        imgsrc: 'static/pic2.jpg',
                    },
                    {
                        title: '想让Kronos变得更好？',
                        summary: 'Kronos是一款开源的移动应用程序，如果你想让它变得更好你可以加入Github社区提出意见或是贡献代码',
                        buttom: '前往项目主页',
                        href: "https://github.com/MrChinico",
                        imgsrc: 'static/pic.jpg'
                    },
                    {
                        title: '实例标题',
                        summary: '实例说明：这是一段实例说明你可以在这里填写你想填写的项目说明',
                        buttom: '实例按钮',
                        imgsrc: 'static/pic3.jpg'
                    }
                ],
                currentIndex: 0
            }
        },
        methods: {
            autoPlay(){
                this.currentIndex++
                if(this.currentIndex > this.items.length - 1)
                    this.currentIndex = 0
            }
        },
        created(){
            this.$nextTick(() => {
                this.timer = setInterval(() => {
                    this.autoPlay()
                }, 7000)
            })
        },
        
    }
</script>

<style scoped>
    
</style>





