<template>
    <header id="public-header">
        <div class="header-info">
            <div class="logo">Kronos</div>
            <div class="hub" @click="menu = !menu">
                <i class="fas fa-bars"></i>
            </div>
            <nav class="header-navigation wow fadeInRight"  v-if="menu">
                <li v-for="navigation in navigations"><a :href="navigation.herf">{{navigation.name}}</a></li>
                <div class="user-center">
                    <div><a @click="show = !show">登陆</a></div>
                    <div><a href="#/signup">注册</a></div>
                    <transition name="form">
                        <PublicForm v-if="show"/>
                    </transition>
                </div>
            </nav>
        </div>
    </header>
</template>

<script>
    import PublicBanner from './PublicBanner'
    import PublicForm from './PublicForm'

    export default {
        name: 'PublicHeader',
        data(){
            return{
                menu: true,
                navigations: [{
                    name: '首页',
                    herf: '#/'
                },
                {
                    name: '产品',
                    herf: '#/introduction'
                },
                {
                    name: '博客',
                    herf: '#/blog'
                }],

                show: false
            }
        },
        components: {
            PublicBanner,
            PublicForm
        },
        methods: {
            showForm: function(type){
                if(type == 'signin'){
                    if(this.show = !this.show);
                        
                }
                else{
                    if(this.show = !this.show);
                
                }
            }
        }
    }
</script>

<style scoped>
</style>





