<template>
    <div id="blog">
        <div class="main">
            <transition-group tag="ul" class="index">
                <li
                    v-for="(item, index) in blogs"
                    :key="index">
                    <h1><router-link :to="{name: 'Details', params:{id: item.ID}}">{{item.title}}</router-link><span>{{item.date}}</span></h1>
                    <div class="sort">
                        <span v-for="sort in item.tag">{{sort}}</span>
                    </div>
                    <div v-html="item.content.substring(0,60)+'……'"></div>
                </li>
            </transition-group>
            <div class="archive">
                <div>
                    <h1>归档</h1>
                    <ul>
                        <li
                            v-for="(item, index) in blogs"
                        >
                            <span>{{item.date}}</span>
                            <router-link :to="{name: 'Details', params:{id: item.ID}}">
                                {{item.title}}
                            </router-link>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <PublicFooter/>
    </div>
</template>

<script>
    import PublicFooter from '@/components/public/PublicFooter'
    export default {
        components: {
            PublicFooter
        },
        mounted(){
            this.$http.get('./static/data/blogs.json')
            .then(response => {
            console.log(response);
            this.blogs = response.data
            console.log(this.items)
            })
            .catch(function (error) {
            console.log(error);
            });
        },
        methods: {

        },
        data() {
            return {

                blogs: [],
                
            }
        }
    }
</script>

<style scoped>
</style>





